















.dokan-dashboard .widgets-wrapper {
  display: block;
  overflow: hidden;
  margin-top: 15px;
  width: 100%;
}
.dokan-dashboard .dokan-postbox {
  width: calc(50% - 15px);
  margin-right: 20px;
  float: left;
}
.dokan-dashboard .dokan-postbox:nth-child(n+2) {
  margin-right: 0;
}
.dokan-dashboard .dokan-postbox:nth-child(2n+1) {
  clear: both;
}
.dokan-dashboard .dokan-postbox .loading {
  display: block;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}






































































.dokan-pro-features .pro-feature-wrap {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 15px;
}
.dokan-pro-features .pro-feature {
  min-width: 48%;
  max-width: 48%;
  margin-bottom: 20px;
  border: 1px solid #E6E6E6;
  background: #fff;
}
@media screen and (max-width: 782px) {
.dokan-pro-features .pro-feature {
    min-width: 100%;
    max-width: 100%;
}
}
.dokan-pro-features .pro-feature:nth-child(2n) {
  margin-right: 0px;
}
.dokan-pro-features .pro-feature-thumb img {
  width: 100%;
  height: auto;
}
.dokan-pro-features .pro-detail {
  padding: 10px 20px;
  min-height: 100px;
  background: #fff;
}
.dokan-pro-features .pro-detail h3.title {
  margin: 5px 0 10px;
  padding: 0;
}
.dokan-pro-features .pro-detail h3.title a {
  text-decoration: none;
  color: #111;
}
.dokan-pro-features .pro-links {
  padding: 10px;
  background: #F5F5F5;
  border-top: 1px solid #E6E6E6;
}
.dokan-pro-features a.button.disabled {
  background: #eee;
}
